.sticky{display:flex;justify-content:end;font-family:"SpoqaHanSansNeo","Noto Sans KR","pretendard","맑은 고딕";font-weight:bold;color:#666;font-size:13px;width:30vw;height:2000px;position:relative}.sticky nav{height:500px;position:sticky;top:0}.sticky nav .perfume1{line-height:0}.sticky nav .perfume2{display:flex;color:#999}.sticky nav .perfume2 li{line-height:8px;padding-right:9px}.sticky nav .fragrance{display:flex;color:#999}.sticky nav .fragrance li{line-height:8px;padding-right:8px}.sticky nav .fragrance li span{color:#666}.sticky nav .sometime{padding-top:10px;position:relative}.sticky nav .sometime::before{content:"";position:absolute;top:3px;width:300px;height:1px;background-color:#afafaf}