.bottom{font-family:"SpoqaHanSansNeo","Noto Sans KR","pretendard","맑은 고딕";position:relative;display:flex;justify-content:center;max-width:1920px}.bottom footer{position:absolute;bottom:0}.bottom footer .footerTop{display:flex;justify-content:space-around}.bottom footer .footerTop::before{content:"";width:60vw;height:1px;position:absolute;background-color:rgba(202,202,202,.8862745098);top:-30px}.bottom footer .footerTop p{font-size:14px;color:#666}.bottom footer .footerTop p:nth-child(2){font-size:12px}.bottom footer .footerBottom{flex-shrink:0}.bottom footer .footerBottom ul{padding-top:30px;display:flex;justify-content:space-between}.bottom footer .footerBottom ul li{font-weight:normal;font-size:10px;color:#666;padding-left:5px}